import License from "./License.vue";
import Vuex from 'vuex/dist/vuex';
import Paginate from 'vuejs-paginate';


export default {
    components: {
        License,
        Paginate
    },
    created() {
        this.refreshView();
    },
    computed: {
        ...Vuex.mapState(['license'])
    },
    methods: {
        refreshView(page=1) {
            this.$store.dispatch('updatePage', page);
        }
    }
}
